@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,600;0,700;0,900;1,600&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Playfair Display", serif;
  /* overflow: hidden; */
}

ul {
  list-style: none;
}

img {
  user-select: none;
  -webkit-user-drag: none;
}

.app {
  background-color: #e6e7ef;
}
